import { Controller, Element } from '@ali/4ever-cangjie';
import { isTable } from '../types';

export default function shouldSelectAll(
  controller: Controller,
  container: Element,
) {
  const { document, selection } = controller.value;
  if (!selection) {
    return false;
  }
  const start = selection.getStart(document);
  const table = document.getClosest(
    start.key,
    (node) => isTable(node) && node !== container,
  );
  return table ? true : undefined;
}
